import axios from 'axios';
import React, { useState, useEffect } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';


// class Detail2 extends React.Component {
//   componentDidMount(){
//     //AJAX 요청 같은 컴포넌트 마운트 전 실행되는 것
//   }

//   componentWillUnmount(){
//     //컴포넌트 마운트 되기 전 실행되는 것
//   }

// }

let 박스 = styled.div`
    padding : 20px;
`;

let 제목 = styled.h4`
    font-size : 25px;
    color : ${props => props.색상}
`;

function Detail(props){ 

    let [isAlertTime, setIsAlertTime] = useState(true);
    let [inputData, setInputData] = useState('');
    //컴포넌트가 마운트 되었을때, 업데이트 될 때, 특정 코드 실행
    useEffect(()=>{//중복사용 가능(순차적 실행), AJAX요청( jQuery,axios,fetch() )할 때 쓰임,
      
      axios.get().then().catch();
      
      setTimeout(()=>{
        setIsAlertTime(false);
      },2000);

      //아래는, Detail 컴포넌트가 언마운트 될 때 실행됨
      //setTimeout 같은 펑션 안의 코드가 '뒤로가기' 같은 이벤트 시 오류 발생하므로 
      //리턴 코드 안에, 타이머 제거해야됨
      return ()=>{ clearTimeout(); }

    },[isAlertTime]);//[] <- 비어있으면 컴포넌트가 한번 랜더링 될때만 실행됨


    let { id } = useParams();
    let product = props.product.find((x)=>{
        return x.id == id;
    });
    
    let history = useHistory();

    return (
        <div className="container">
          <박스><제목 className="red" 색상={'red'}>상세페이지</제목></박스>
          <박스><제목 className="blue">상세페이지</제목></박스>
          
          { inputData }
          <input onChange={ (e)=>{ setInputData(e.target.value) } } />

          {
            isAlertTime === true
            ? <AlertModal />
            : null
          }
          
          <div className="row">
            <div className="col-md-6">
              <img src={'https://codingapple1.github.io/shop/shoes'+(product.id+1)+'.jpg'} width="100%" />
            </div>
            <div className="col-md-6 mt-4">
            <h4 className="pt-5">{product.title}</h4>
              <p>{product.content}</p>
              <p>{product.price}</p>

              <StockInfo stock={ props.stock }></StockInfo>

              <button className="btn btn-danger" onClick={ ()=> {
                let stock = [...props.stock];
                props.setStock();
              }}>주문하기</button> &nbsp;
              <button className="btn btn-danger" onClick={()=>{
                history.goBack();
              }}>뒤로가기</button> 
            </div>
          </div>
        </div> 
    )
  }

  function AlertModal(){
    return(
      <div className="my-alert my-alert-2"> 
        <p>재고가 얼마 남지 않았습니다.</p>
      </div>
    );
  }

  function StockInfo(props){
    return(
      <p>재고 : {props.stock[0]}</p>
    );
  }

export default Detail;