/* eslint-disable */
import React, { useState } from 'react';
import { Navbar, Nav, NavDropdown, Button, Jumbotron } from 'react-bootstrap';
import './App.css';
import Data from './data.js';
import {Link, Route, Switch} from 'react-router-dom';
import Detail from './Detail.js';
import axios from 'axios';

function App() {

  let [data, dataChf] = useState(Data);
  let [stock, setStock] = useState([10,20,30]);

  return (
    <div className="App">
      <Navbar bg="light" expand="lg">
        <Navbar.Brand href="#home">구매 일반자재 시스템</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="mr-auto">
            <Nav.Link as={Link} to="/">Home</Nav.Link>
            <Nav.Link as={Link} to="/detail">Detail</Nav.Link>
            <NavDropdown title="Dropdown" id="basic-nav-dropdown">
              <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
            </NavDropdown>
          </Nav>
          
        </Navbar.Collapse>
      </Navbar>
      
      <Switch>
        <Route exact path="/" >
          <Jumbotron className="jumbo">
            <h1>점보트론 제목</h1>
            <p>
              This is a simple hero unit, a simple jumbotron-style component for calling
              extra attention to featured content or information.
            </p>
            <p>
              <Button variant="primary">Learn more</Button>
            </p>
          </Jumbotron>
          <div className="container">
            <div className="row">
            
                {
                  data.map((a,i)=>{
                    return <Material product={ data[i] } i={i} key={i}/>  
                  })
                }
              
              <button className="btn btn-primary" onClick={()=>{
                
                //로딩중 UI 띄움(true정보 useState활용)
                
                //ajax 요청(post)
                //axios.post('서버URL', {id : 'id정보', pw : 1234}).then().catch();

                //ajax 요청(get)
                //fetch, json객체를 그대로 가져오므로 object로 바꿔주는 추가작업 필요
                //fetch('https://codingapple1.github.io/shop/data2.json').then().catch();
                
                //ajax 요청(get)
                //서버개발자에게 어떤URL로 요청해야되는지 협업
                //axios, json객체를 자동으로 object로 바꿔줌
                //'더보기' 버튼누른 횟수를 useState에 저장하고 url의 숫자를 바꿔주면서 계속해서 더해줌
                axios.get('https://codingapple1.github.io/shop/data2.json')
                .then((result)=>{
                  //성공했을 때 
                  
                  //로딩중 UI 제거(true정보 useState활용)

                  //ajax 요청 후, 데이터 처리
                  console.log('성공'); console.log(result.data);
                  dataChf([...data, ...result.data]); 
                  {
                    data.map((a,i)=>{
                      return <Material product={ data[i] } i={i} key={i}/>  
                    })
                  }
                })
                .catch(()=>{
                  //실패했을 때(콜백함수)

                  //로딩중 UI 제거(true정보 useState활용)
                  
                  //ajax 요청 후, 데이터 처리
                  console.log('실패'); 
                });
              }}>더보기</button>

            </div>
          </div>
        </Route>

        <Route path="/detail/:id">
          <Detail product={ data } stock={ stock } setStock = {setStock} />
        </Route>

        <Route path="/:id">
          <div>
            test
          </div>
        </Route>
      </Switch>
      

      {/* <Route path="/000" component={Modal}><div></div></Route> */}
      
      
    </div>
  );

  

  function Material(props){
    
    return (
      <div className="col-md-4">
        <img src={'https://codingapple1.github.io/shop/shoes'+(props.i+1)+'.jpg'} width="100%"/>
        <h4>{props.product.title}</h4>
        <h4>{props.product.content}</h4>
        <h4>{props.product.price}</h4>
      </div> 
      
    );
  }

  
}

export default App;
